.contact {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: #000;
	padding-top: 212px;
	padding-bottom: 99px;

	@media (max-width: 1024px) {
		padding: 50px 0;
	}
}

.contact__caption {
	width: 35%;
	margin: 0 auto;
	margin-bottom: 67px;
	color: #404040;
	text-align: center;
	font-feature-settings: 'liga' off;
	font-size: 48px;
	font-weight: 400;
	line-height: 120%; /* 57.6px */

	@media (max-width: 1570px) {
		width: 100%;
	}

	@media (max-width: 768px) {
		margin-bottom: 30px;
		font-size: 30px;
	}
}

.contact__form-input {
	width: 297px;
	padding-left: 16px;
	border-radius: 5px 0px 0px 5px;
	border: 1px solid #000;
	background: #FFF;

	@media (max-width: 768px) {
		width: 200px;
	}
}

.contact__form-field {
	display: flex;
	justify-content: center;
}