.scrolled {
	margin-top: 192px;
	height: 887px;
	background: linear-gradient(180deg, rgba(255,255,255,1) 10%, #232323 10%);

	@media (max-width: 768px) {
		margin-top: 50px;
		height: 500px;
	}
}

.scrolled__block {
	position: relative;
	margin: 0 auto;
	max-width: 1335px;
	height: 90%;
	background-color: #fff;
	border-radius: 16px;
	overflow: hidden;
}

.scrolled__block-top {
	position: absolute;
	top: 0;
	z-index: 1;
	display: flex;
	width: 100%;
	align-items: center;
	padding: 15px 20px;
	border-top-right-radius: 16px;
	border-top-left-radius: 16px;
	background-color: #F7F7F7;
}

.scrolled__block-dot {
	width: 16px;
	height: 16px;
	flex-shrink: 0;
	background-color: #C4C4C4;
	opacity: 0.4;
	border-radius: 50%;
	margin-right: 6px;
}

.scrolled__content {
	display: flex;
	gap: 25px;
	justify-content: center;
	margin: 0 auto;

	& img {
		width: 175px;
		height: 175px;
		object-fit: cover;

		@media (max-width: 768px) {
			width: 75px;
			height: 75px;
		}
	}
}

.scrolled__line {
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 25px;

	&--1 {top: 50px;}
	&--2 {top: 20px;}
	&--3 {top: 100px;}
	&--4 {top: 80px;}
}

.scrolled__line-content {
	display: flex;
	gap: 25px;
	flex-direction: column;
	width: 175px;

	@media (max-width: 768px) {
		width: 75px;
	}

	animation: scroll 10s infinite linear;
}

// // для включения анимации по наведеню убрать ее из блока выше, и добавить тут
// .scrolled__block:hover {
