.about {
	position: relative;
	min-height: 961px;
	padding-bottom: 176px;

	@media (max-width: 1570px) {
		min-height: calc(961px + 250px);
	}

	@media (max-width: 1024px) {
		padding: 0;
		min-height: auto;
	}
}

.about__wrapper {
	display: flex;
	flex-shrink: 0;

	@media (max-width: 1024px) {
		flex-direction: column;
		// что бы сначала шла картинка - ставим колум реверс
		// flex-direction: column-reverse;
	}

	&--team {
		flex-direction: row-reverse;

		@media (max-width: 1570px) {
			flex-direction: column;
			align-items: center;
		}
	}
}

.about__img {
	position: absolute;
	right: 0;
	top: 0;

	@media (max-width: 1570px) {
		top: 250px;
	}

	@media (max-width: 1024px) {
		top: auto;
		margin-left: auto;
		position: relative;
		right: -20px;
		flex-shrink: 0;

		& img {
			height: 400px;
		}
	}
}

.about__img--team {
	left: 0;
	right: auto;

	@media (max-width: 1570px) {
		position: relative;
		top: auto;
		width: 100vw;
		margin-top: 40px;

		& img {
			height: 600px;
			width: 100%;
			object-fit: cover;
		}
	}

	@media (max-width: 1024px) {
		top: auto;
		margin-left: auto;
		position: relative;
		left: -20px;
		flex-shrink: 0;

		& img {
			height: 300px;
		}
	}

	@media (max-width: 767px) {
		width: 120%;
		right: 0;
		left: -20px;

		& img {
			height: auto;
		}
	}
}

.about__img-block {
	position: absolute;
	top: calc(50% - 229px);
	left: -229px;
	display: flex;
	width: 456px;
	height: 456px;
	flex-wrap: wrap;

	@media (max-width: 1024px) {
		width: 160px;
		height: 160px;
		left: -30px;
		top: calc(50% - 80px);
	}
}

.about__img-block-item {
	position: relative;
	width: 50%;
	overflow: hidden;

	& img {
		width: 101%;
		height: 101%;
		position: absolute;
		inset: 0;
		object-fit: cover;
	}
}

.about__content {
	width: 34%;
	margin-top: 226px;

	@media (max-width: 1570px) {
		width: 100%;
		text-align: center;
		margin-top: 0;
	}

	@media (max-width: 1024px) {
		padding: 0 0 30px 0;
	}

	&--team {
		@media (max-width: 1570px) {
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}
}

.about__caption {
	color: #404040;
	font-size: 48px;
	font-style: normal;
	font-weight: 400;
	line-height: 110%; /* 52.8px */

	@media (max-width: 767px) {
		font-size: 30px;
	}
}

.about__description {
	color: #6E6E6E;
	font-size: 16px;
	font-weight: 400;
	line-height: 160%; /* 25.6px */
}