/* Слайдер */
.slick-slider {
	width: 100vw;
	position: absolute;
	inset: 0;
	z-index: -1;
}

.slick-list {
	overflow: hidden;
}

/* Лента слайдов */
.slick-track {
	display: flex;
}

/* Слайд */
.slick-slide {
	display: flex;
	height: 100%;
}

/* Точки (булиты) */
.slick-dots {
	position: absolute;
	bottom: 56px;
	display: flex;
	justify-content: center;
	align-self: center;
	background-color: transparent;
}

.slick-dots li{
	list-style: none;
	margin: 0 11px;
}

/* Активная точка */
.slick-dots li.slick-active button{
	opacity: .9;
}

/* Элемент точки */
.slick-dots li button {
	width: 70px;
	height: 8px;
	border-radius: 3px;
	border: none;
	font-size: 0;
	background-color: #fff;
	opacity: .3;
pointer-events: none;
}

.slider__content {
	margin: 0 auto;
	display: flex;
	align-items: center;
	text-align: center;
	justify-content: center;
}

.slider__img {
	width: 100vw;
	height: 100vh;
	object-fit: cover;
}

.slider__text {
	font-size: 24px;
	line-height: 28px;
	text-align: left;
}
