.furniture {
	padding-top: 154px;
	padding-bottom: 115px;
	color: #000;
	text-align: center;

	@media (max-width: 1024px) {
		padding: 50px 0;
	}
}

.furniture__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.furniture__caption {
	font-size: 48px;
	font-weight: 400;
	line-height: 110%; /* 52.8px */

	@media (max-width: 1024px) {
		font-size: 30px;
	}
}

.furniture__description {
	color: #6E6E6E;
	text-align: center;
	font-size: 16px;
	line-height: 160%; /* 25.6px */
}

.furniture__info {
	padding: 157px 0 165px 0;

	@media (max-width: 1024px) {
		padding: 45px 0;
	}
}

.furniture__info-list {
	@include list-style;
	display: flex;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;

	@media (max-width: 768px) {
		flex-direction: column;
	}
}

.furniture__info-item {
	width: 25%;

	@media (max-width: 768px) {
		width: 100%;
	}
}

.furniture__info-name {
	color: #000;
	font-size: 26px;
	font-weight: 400;
	line-height: 26px; /* 100% */
}

.furniture__info-description {
	color: #6E6E6E;
	font-size: 16px;
	font-weight: 400;
	line-height: 160%; /* 25.6px */
}