.portfolio {
	color: #000;
	padding-top: 128px;

	@media (max-width: 1024px) {
		padding-top: 50px;
	}
}

.portfolio__top {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;	
	margin-bottom: 46px;
	
	@media (max-width: 1024px) {
		margin-bottom: 0;
	}
}

.portfolio__controls {
	display: flex;
	margin-right: -12px;
}

.portfolio__control {
	display: flex;
	align-items: center;
	width: 42px;
	height: 42px;
	border: none;
	pointer-events: none;
	background-color: transparent;

	&--prev {
		@media (max-width: 768px) {
			display: none;
		}
	}
}

.portfolio__caption {
	color: #000;
	font-size: 26px;
	font-weight: 400;
	line-height: 120%; /* 31.2px */

	@media (max-width: 1024px) {
		font-size: 22px;
	}
}

.portfolio__description {
	margin: 0;
	color: #6E6E6E;
	font-size: 16px;
	font-weight: 400;
	line-height: 32px; /* 200% */

	@media (max-width: 1024px) {
		font-size: 12px;
		line-height: 22px; /* 200% */
	}
}

.portfolio__list {
	@include list-style;
	display: flex;
	width: 100%;
	gap: 40px;

	@media (max-width: 768px) {
		overflow-x: scroll;
	}
}

.portfolio__item {
	width: 25%;

	@media (max-width: 768px) {
		flex-shrink: 0;
		width: 100%;
	}
}

.portfolio__prev {
	@media (max-width: 768px) {
		& img {
			width: 100%;
			object-fit: cover;
		}
	}
}