.footer {
	color:#000;
}

.footer__body {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 36px 0;
	width: 100%;
	border-top: 1px solid #000;
}

.footer__social {
	@include list-style;
	gap: 26px;
	display: flex;
}

.footer__social-item {
	display: flex;
	width: 40px;
	height: 40px;

	& svg {
		transition: opacity .3s;
	}

	&:hover {
		& svg {
			opacity: .6;
		}
	}
}