.reviews {
	padding: 125px 0 156px 0;
	background-color: #F1E9E6;

	@media (max-width: 1024px) {
		padding: 50px 0;
	}
}

.reviews__caption {
	color: #404040;
	text-align: center;
	font-size: 48px;
	font-style: normal;
	font-weight: 400;
	line-height: 120%; /* 57.6px */
}

.reviews__list {
	@include list-style;
	display: flex;
	gap: 50px;
	max-width: 830px;
	margin: 0 auto;
	flex-direction: column;
}

.reviews__item {
	display: flex;
	gap: 50px;

	&:nth-child(even) {
		flex-direction: row-reverse;

		@media (max-width: 768px) {
			flex-direction: column;
			align-items: center;
			gap: 20px;
		}
	}

	@media (max-width: 768px) {
		font-size: 24px;
	}

	@media (max-width: 768px) {
		flex-direction: column;
		align-items: center;
		gap: 20px;
	}
}

.reviews__content {
	position: relative;
	background-color: #fff;
	border-radius: 10px;
	padding: 32px 43px;

	&::before {
		@include pseudo;
		top: 45px;
		left: -20px;
		width: 0; 
		height: 0;
		border-top: 20px solid #fff;
		border-left: 20px solid transparent;

		@media (max-width: 768px) {
			display: none;
		}
	}
}

.reviews__item:nth-child(even) {
	.reviews__content {	
		&::before {
			left: auto;
			right: -20px;
			border-top: 20px solid #fff;
			border-right: 20px solid transparent;

			@media (max-width: 768px) {
				display: none;
			}
		}
	}
}

.reviews__author {
	width: 100px;
	height: 100px;
	flex-shrink: 0;
	border-radius: 50%;
}

.reviews__author-name {
	color: #000;
	font-size: 20px;
	font-weight: 600;
	line-height: 26px; /* 130% */
	margin: 0;
}

.reviews__text {
	color: #6E6E6E;
	font-size: 16px;
	font-weight: 400;
	line-height: 160%; /* 25.6px */
}