.btn {
	display: flex;
	min-height: 56px;
	padding: 16px 65px;
	border: 1px solid #FFF;
	background-color: transparent;

	justify-content: center;
	align-items: center;
	text-align: center;

	color: #FFF;
	text-align: center;
	font-family: Sarala;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 24px; /* 133.333% */

	cursor: pointer;
	transition: border-radius .3s;

	&:hover {
		border-radius: 8px;
	}

	&--black {
		background-color: #000;
		color: #fff;
		margin: 42px 0 0 0;

		@media (max-width: 768px) {
			margin: 24px auto 0 auto;
		}
	}

	&--submit {
		border: 1px solid #000;
		padding: 16px 20px;
		margin-left: -2px;
		margin-right: 0;
		width: 186px;
		height: 56px;
		margin-top: 0;
		border-radius: 0px 5px 5px 0px;

		@media (max-width: 768px) {
			width: 120px;
		}

		&:hover {
			border-radius: 0px 5px 5px 0px;
			border-top-right-radius: 0px;
			border-bottom-right-radius: 0px;
		}
	}
}