.services {
	color: #404040;
	padding: 115px 0 174px 0;

	@media (max-width: 1024px) {
		padding: 55px 0 70px 0;
	}
}

.services__caption {
	position: relative;
	width: 60%;
	padding: 60px 0;
	margin: 0 auto;
	text-align: center;
	font-family: Montserrat;
	font-size: 28px;
	font-weight: 400;
	line-height: 120%; /* 33.6px */

	&::before,
	&::after {
		@include pseudo;
		top: 0;
		left: calc(50% - 70px);
		width: 140px;
		height: 1px;
		background-color: #000;
	}

	&::after {
		top: 100%;
	}

	@media (max-width: 1024px) {
		width: 100%;
		padding: 30px 0;
	}

	@media (max-width: 768px) {
		font-size: 24px;
	}
}

.services__list {
	@include list-style;
	display: flex;
	margin-top: 130px;
	width: 100%;
	justify-content: space-between;

	@media (max-width: 1024px) {
		margin-top: 50px;
	}

	@media (max-width: 768px) {
		font-size: 24px;
		flex-direction: column;
		gap: 20px;
	}
}

.services__item {
	display: flex;
	width: 20%;
	flex-direction: column;

	@media (max-width: 1024px) {
		width: 30%;
	}

	@media (max-width: 768px) {
		width: 100%;
	}
}

.services__index {
	color: #F1EDEA;
	font-family: Open Sans;
	font-size: 144px;
	line-height: 144px; /* 100% */
}

.services__name {
	color: #000;
	font-size: 26px;
	font-weight: 400;
	line-height: 26px; /* 100% */
	margin: 36px 0 16px 0;
}

.services__description {
	color: #6E6E6E;
	font-size: 16px;
	font-weight: 400;
	line-height: 160%; /* 25.6px */
	margin: 0;
}