.hero {
	display: flex;
	width: 100%;
	position: relative;
	margin-top: -123px;
	min-height: 100vh;
	padding-top: 290px;

	@media (max-width: 1024px) {
		padding-top: 200px;
	}

	@media (max-width: 768px) {
		padding-top: 150px;
		margin-top: -80px;
	}
}

.hero__caption {
	font-size: 60px;
	font-weight: 400;
	line-height: 120%; /* 72px */
	margin: 0 0 57px 0;

	@media (max-width: 1024px) {
		font-size: 30px;
		margin: 0 0 30px 0;
	}
}

.hero__content {
	width: 65%;
	padding-left: 119px;
	position: relative;

	@media (max-width: 1024px) {
		padding-left: 50px;
	}

	@media (max-width: 768px) {
		width: 100%;
		padding: 0;
	}
}